<template>
  <div>
    <div class="text-right">
      <Button
        view="smooth"
        color="teal"
        @click="handleAddBankTransferInfo"
        :loading="loadingPaymentMethods"
        dense
        >Add Bank</Button
      >
    </div>
    <hr class="mt-5 mb-8 border-grey-01" />
    <div
      v-loading="loading.all"
      v-if="state.banks.length > 0"
      class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-2 xl:grid-cols-3 gap-4"
    >
      <Card v-for="(item, index) in state.banks" :key="index">
        <div class="flex mb-4">
          <p class="font-semibold mb-0">{{ item.full_name }}</p>
          <div class="flex ml-auto gap-2">
            <Button
              view="smooth"
              color="blue"
              @click="editBank(item), openModal('form')"
              dense
            >
              <i class="fas fa-edit"></i>
            </Button>
            <Button
              view="smooth"
              color="red"
              @click="openModalSelected('delete', item)"
              dense
            >
              <i class="fas fa-trash"></i>
            </Button>
          </div>
        </div>
        <div
          v-for="attribute of item.bank_transfer"
          :key="attribute.attribute_name"
        >
          <p class="small mb-0">{{ attribute.name }}:</p>
          <p class="mb-3">{{ attribute.value }}</p>
        </div>
      </Card>
    </div>
    <p v-else class="text-center text-red-base font-bold">
      There's no available data to show.
    </p>
    <Modal
      v-model="modal.form"
      :title="selected === null ? 'Add Bank Transfer' : 'Edit Bank Transfer'"
      @close="unsetData"
    >
      <BankTransferForm
        ref="refBankTransferForm"
        :data="data"
        @update:data="handleFormUpdate"
        :attributes="
          isBankTransferFormUpdate
            ? selectedBankTranfserAttributes
            : bankTransferAttributes
        "
      />
      <template #footer>
        <Button
          v-if="selected === null"
          :loading="loading.form"
          @click="createBankTransfer"
          color="teal"
          view="smooth"
          >Add Bank Transfer</Button
        >
        <Button
          v-else
          :loading="loading.form"
          @click="updateBankTransfer"
          color="teal"
          view="smooth"
          >Update Bank Transfer</Button
        >
      </template>
    </Modal>
    <Modal
      v-model="modal.delete"
      size="xs"
      title="Delete Bank"
      @close="unsetData"
    >
      <p class="my-5">Are you sure want to delete this bank data?</p>
      <template #footer>
        <Button
          :loading="loading.delete"
          view="smooth"
          color="red"
          @click="deleteBankTransfer"
          >Delete</Button
        >
      </template>
    </Modal>
  </div>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useUser } from 'composable/user'
import {
  useShowPaymentInfo,
  useShowPaymentInfoBankTransfer
} from 'composable/customer'
import { usePaymentMethods } from 'composable/setting'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import BankTransferForm from './BankTransfer/Form.vue'

export default defineComponent({
  components: {
    Button,
    Card,
    Modal,
    BankTransferForm
  },
  setup() {
    const { bankTransferConfig } = useUser()
    const { state } = useShowPaymentInfo()
    const {
      data,
      loading,
      modal,
      selected,
      openModal,
      unsetData,
      actionAll,
      actionCreate,
      actionUpdate,
      actionDelete
    } = useShowPaymentInfoBankTransfer()
    const {
      state: statePaymentMethods,
      loading: loadingPaymentMethods,
      action: actionPaymentMethods
    } = usePaymentMethods()
    const route = useRoute()
    const refBankTransferForm = ref(null)
    const selectedBankTranfserAttributes = ref([])

    const bankTransferAttributes = computed(() => {
      const bankTransferPaymentMethod = statePaymentMethods.value.find(
        (paymentMethod) =>
          paymentMethod.name === 'Bank Transfer' && paymentMethod.id === 4
      )
      if (bankTransferPaymentMethod) {
        return bankTransferPaymentMethod.attributes
      }
      return []
    })
    const isBankTransferFormUpdate = computed(() => !!selected.value)

    const createBankTransfer = async () => {
      const isValid = refBankTransferForm.value?.validate()
      if (isValid)
        await actionCreate(route.params.customerId, async () => {
          await actionAll(route.params.customerId)
        })
    }
    const updateBankTransfer = async () => {
      const isValid = refBankTransferForm.value?.validate()
      if (isValid)
        await actionUpdate(route.params.customerId, async () => {
          await actionAll(route.params.customerId)
        })
    }
    const deleteBankTransfer = async () => {
      await actionDelete(route.params.customerId, async () => {
        await actionAll(route.params.customerId)
      })
    }
    const editBank = (item) => {
      if (item.bank_transfer) {
        for (let attribute of item.bank_transfer) {
          data[attribute.attribute_name] = attribute.value
        }
        selectedBankTranfserAttributes.value = item.bank_transfer
      }
      selected.value = item.id
    }
    const openModalSelected = (target, item) =>
      openModal(target, () => {
        selected.value = item.id
      })
    const handleAddBankTransferInfo = async () => {
      await actionPaymentMethods()
      openModal('form')
    }
    const handleFormUpdate = (newData) => {
      Object.assign(data, newData)
    }

    return {
      refBankTransferForm,
      data,
      modal,
      selected,
      loading,
      loadingPaymentMethods,
      openModal,
      unsetData,
      state,
      statePaymentMethods,
      selectedBankTranfserAttributes,
      bankTransferAttributes,
      isBankTransferFormUpdate,
      editBank,
      bankTransferConfig,
      openModalSelected,
      createBankTransfer,
      updateBankTransfer,
      deleteBankTransfer,
      handleAddBankTransferInfo,
      handleFormUpdate
    }
  }
})
</script>
