<template>
  <div>
    <Breadcrumb
    class="mb-8 mt-4"
    pageTitle="Edit Customer"
    :backTo="{ name: 'layoutCustomer' }"
    :items="breadcrumb">
      <template #right>
        <Button color="teal" :loading="loadingUpdate" @click="updateCustomer">Save Changes</Button>
      </template>
    </Breadcrumb>
    <Card>
      <div class="space-y-10">
        <div class="space-y-5">
          <div class="flex">
            <div class="flex w-10 h-10 bg-teal-04 rounded-full text-white font-semibold">
              <div class="my-auto mx-auto">1</div>
            </div>
            <h6 class="text-lg text-teal-04 ml-5 my-auto">
              Personal Information
            </h6>
          </div>
          <!-- <CustomerForm :data="{...data, address: data.address_text}" class="mb-4 ml-14" ref="refForm" /> -->
          <CustomerForm v-model:data="data" class="mb-4 ml-14" ref="refForm" />
        </div>
        <div class="space-y-5">
          <div class="flex">
            <div class="flex w-10 h-10 bg-teal-04 rounded-full text-white font-semibold">
              <div class="my-auto mx-auto">2</div>
            </div>
            <h6 class="text-lg text-teal-04 ml-5 my-auto">
              Payout Information
            </h6>
          </div>
          <div class="grid grid-cols-5 gap-4 border border-grey-01 rounded-lg ml-14">
            <div class="xl:col-span-1 lg:col-span-2 col-span-5 xl:border-r lg:border-r  border-r-0 border-grey-01 p-5">
              <p class="mb-3 font-semibold ml-3">Choose Payout Method</p>
              <Tab v-model="paymentMethod" :by="null" :items="statePaymentMethods" @change="resetData" :use-external-icon="true" component type="vertical" />
            </div>
            <div class="xl:col-span-4 lg:col-span-3 col-span-5 p-5">
              <component v-if="paymentMethod.is_custom" v-bind="vBind" v-on="vOn" ref="refPaymentMethod" :is="paymentMethod.component" />
              <component v-else v-bind="vBind" v-on="vOn" ref="refPaymentMethod" :is="paymentMethod.component" />
            </div>
          </div>
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
import {computed, defineComponent, ref, isReactive, reactive, watch} from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useShow, useShowPaymentInfo, useUpdate } from 'composable/customer'
import { usePaymentMethods } from 'composable/setting'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import CustomerForm from 'components/Customer/Form.vue'
import AmazonGiftCard from 'components/PaymentMethod/AmazonGiftCard.vue'
import BankTransfer from 'components/Customer/PaymentInfo/BankTransfer.vue'
import PaperCheck from 'components/PaymentMethod/PaperCheck.vue'
import ExpressCheck from 'components/PaymentMethod/PaperCheck.vue'
import PayPal from 'components/PaymentMethod/PayPal.vue'
import Swish from 'components/PaymentMethod/Swish.vue'
import SquareCash from 'components/PaymentMethod/SquareCash.vue'
import Venmo from 'components/PaymentMethod/Venmo.vue'
import Coinbase from 'components/PaymentMethod/Coinbase.vue'
import Zelle from 'components/PaymentMethod/Zelle.vue'
import CashApp from 'components/PaymentMethod/CashApp.vue'
import Cash from 'components/PaymentMethod/Cash.vue'
import ApplePay from 'components/PaymentMethod/ApplePay.vue'
import GooglePay from 'components/PaymentMethod/GooglePay.vue'
import FacebookPay from 'components/PaymentMethod/FacebookPay.vue'
import StoreCredit from 'components/PaymentMethod/StoreCredit.vue'
import VirtualDebitCard from 'components/PaymentMethod/VirtualDebitCard.vue'
import GiftCard from 'components/PaymentMethod/GiftCard.vue'
import InteraceTransfer from 'components/PaymentMethod/InteracTransfer.vue'
import Donate from 'components/PaymentMethod/Donate.vue'
import Bill from 'components/PaymentMethod/Bill.vue'
import ECheck from 'components/PaymentMethod/Echeck.vue'
import Rebate from 'components/PaymentMethod/Rebate.vue'
import Custom from 'components/PaymentMethod/Custom.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Button,
    Card,
    Tab,
    CustomerForm,
    AmazonGiftCard,
    BankTransfer,
    PaperCheck,
    ExpressCheck,
    PayPal,
    Swish,
    SquareCash,
    Venmo,
    Coinbase,
    Zelle,
    CashApp,
    Cash,
    ApplePay,
    GooglePay,
    FacebookPay,
    StoreCredit,
    VirtualDebitCard,
    GiftCard,
    InteraceTransfer,
    Donate,
    Bill,
    ECheck,
    Rebate,
    Custom,
    Breadcrumb
  },
  async setup() {
    const refForm = ref(null)
    const route = useRoute()
    const router = useRouter()
    const { data: dataShow, loading, action } = useShow()
    const { state: statePaymentInfo, data: dataPaymentInfo, loading: loadingPaymentInfo, action: actionPaymentInfo, actionUpdate: actionUpdatePaymentInfo, actionUpdateCustom: actionUpdatePaymentInfoCustom} = useShowPaymentInfo()
    const { state: statePaymentMethods, action: actionPaymentMethods } = usePaymentMethods()
    const { action: actionUpdate, loading: loadingUpdate } = useUpdate()

    await Promise.all([
      action({ id: route.params.customerId, is_store: 1 }),
      actionPaymentInfo(route.params.customerId),
      actionPaymentMethods()
    ])

    const backTo = reactive(route)
    watch(
      () => ({
        name: route.name,
        query: route.query,
        params: route.params
      }),
      (to, from) => {
        backTo.value = from
      }
    )

    const initData = Object.assign({}, dataPaymentInfo.value)
    const refPaymentMethod = ref(null)
    const paymentMethod = ref(null)
    const updateCustomer = async() => {
      if(refForm.value.validate() && (!refPaymentMethod.value.validate || (refPaymentMethod.value.validate && refPaymentMethod.value.validate()))){
        await actionUpdate(route.params.customerId, {
          ...data,
        })
        if (paymentMethod.value.is_custom) {
          await actionUpdatePaymentInfoCustom(route.params.customerId, {
            ...refPaymentMethod.value.data
          })
        } else {
          await actionUpdatePaymentInfo(route.params.customerId, {
            ...dataPaymentInfo.value,
            payment_method: paymentMethod.value.name
          })
        }
        ElMessage({
          message: 'Data has been updated!',
          type: 'success'
        })
        if (!!backTo.value && !!backTo.value.name) {
          router.push({
            name: backTo.value.name,
            params: backTo.value.params
          })
        } else {
          router.push({name: 'layoutCustomer'})
        }
      }
    }
    paymentMethod.value = statePaymentMethods.value[0]
    const vOn = computed(() => {
      const on = {}
      if(paymentMethod.value.component !== 'BankTransfer') on['update:data'] = e => dataPaymentInfo.value = e

      return on
    })
    const vBind = computed(() => {
      const bind = {}
      if(paymentMethod.value.component !== 'BankTransfer' && !paymentMethod.value.is_custom) bind.data = dataPaymentInfo.value

      if (paymentMethod.value.is_custom) {
        const customPayout = statePaymentInfo.value.custom && statePaymentInfo.value.custom.custom_payout && [...statePaymentInfo.value.custom.custom_payout].shift() || {}
        bind.data = {
          [customPayout.attribute_name]: customPayout.value
        }
        bind.payout = paymentMethod.value
      }

      return bind
    })
    const data = isReactive(dataShow) ? dataShow : reactive({...dataShow.value})

    const breadcrumb = [{
      text: 'Customer',
      to: {
        name: 'layoutCustomer'
      }
    }, {
      text: 'Edit Customer'
    }]

    const resetData = () => {
      dataPaymentInfo.value = Object.assign({}, initData)
    }

    return {
      data,
      dataPaymentInfo,
      loadingUpdate,
      paymentMethod,
      statePaymentInfo,
      statePaymentMethods,
      vOn,
      vBind,
      updateCustomer,
      resetData,
      breadcrumb,
      refForm,
      refPaymentMethod,
      backTo
    }
  }
})
</script>