<template>
  <div>
    <div
      v-for="attribute of attributes" class="mb-4"
      :key="attribute.attribute_name">
      <Input
        v-model="data[attribute.attribute_name]"
        type="text"
        :label="attribute.name"
        class="w-full"
        :placeholder="attribute.name"
        :disabled="disabled"
        :view="
          (validation[attribute.attribute_name].$invalid && 'danger') ||
          'default'
        "
      />
      <small
        class="text-red-base block mt-2"
        v-for="(error, errorIndex) of validation[attribute.attribute_name]
          .$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, watch, computed, onMounted } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import { useUser } from 'composable/user'
import { useVuelidate } from '@vuelidate/core'
import { buildBankValidationRules } from '@/validations/index'

export default defineComponent({
  components: {
    Input,
    Select
  },
  props: {
    list: {
      type: Array,
      default: () => []
    },
    data: {
      type: Object,
      default: () => ({})
    },
    disabled: {
      type: Boolean,
      default: false
    },
    attributes: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:data'],
  setup(props, { emit }) {
    const { bankTransferConfig, isUseIBAN } = useUser()
    const data = reactive({})

    const rules = computed(() => buildBankValidationRules(props.attributes))

    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const setInitialState = () => {
      for (let attribute of props.attributes) {
        if (Object.keys(props.data).includes(attribute.attribute_name)) {
          data[attribute.attribute_name] = props.data[attribute.attribute_name]
          continue
        }
        data[attribute.attribute_name] = ''
      }
    }

    watch(
      () => data,
      () => {
        emit('update:data', data)
      },
      {
        deep: true
      }
    )
    watch(
      () => JSON.stringify(props.attributes),
      () => {
        setInitialState()
      },
      {
        immediate: true, deep: true
      }
    )

    return {
      data,
      validation,
      validate,
      bankTransferConfig,
      isUseIBAN
    }
  }
})
</script>
